@import "../css/normalize.css";
@font-face {
  font-family: "Golos";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("GolosText Regular"), local("GolosText-Regular"), url("../fonts/Golos/GolosText-Regular.woff") format("woff"), url("../fonts/Golos/GolosText-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Golos";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("GolosText Medium"), local("GolosText-Medium"), url("../fonts/Golos/GolosText-Medium.woff") format("woff"), url("../fonts/Golos/GolosText-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Golos";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("GolosText Demibold"), local("GolosText-Demibold"), url("../fonts/Golos/GolosText-Demibold.woff") format("woff"), url("../fonts/Golos/GolosText-Demibold.woff2") format("woff2");
}
@font-face {
  font-family: "Museo Sans Cyrl";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Museo Sans Cyrl Medium"), local("Museo Sans Cyrl-Medium"), url("../fonts/Museo/MuseoSansCyrl-500.woff") format("woff"), url("../fonts/Museo/MuseoSansCyrl-500.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Golos", sans-serif;
  font-style: normal;
  font-weight: 400;
  background: #7700d1;
}

.open-menu .main-nav {
  display: block;
}
.open-menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #4c0085;
  opacity: 0.97;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: rem(0 29px);
}
@media (min-width: 1210px) {
  .container {
    max-width: 1210px;
    padding: rem(0 20px);
  }
}

.header {
  position: relative;
  margin-bottom: rem(25px);
}
@media (min-width: 1210px) {
  .header {
    margin: rem(36px 0 102px);
  }
}

@media (min-width: 1210px) {
  .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header__logo-wrap {
  order: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: rem(17px 34px);
  margin-bottom: rem(19px);
  background: #ffffff;
  border-radius: rem(0 0 43px 43px);
}
@media (min-width: 1210px) {
  .header__logo-wrap {
    position: relative;
    display: block;
    width: 18.8%;
    padding: 0;
    margin: 0;
    background: none;
    border-radius: 0;
  }
  .header__logo-wrap::after {
    content: "";
    position: absolute;
    top: -42px;
    left: 0;
    width: 100%;
    height: rem(123px);
    background: #ffffff;
    border-radius: rem(0 0 43px 43px);
  }
}

@media (min-width: 1210px) {
  .header__logo img {
    position: relative;
    z-index: 10;
    margin: 0 auto;
  }
}

.header__button-open {
  display: block;
  width: rem(28px);
  height: rem(21px);
  background: url("../img/icons/sandwich.svg") no-repeat;
  background-size: cover;
  border: none;
}
@media (min-width: 1210px) {
  .header__button-open {
    display: none;
  }
}

.main-nav {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  display: none;
}
@media (min-width: 1210px) {
  .main-nav {
    order: 1;
    display: block;
    position: static;
    width: auto;
    margin-right: rem(45px);
  }
}

.main-nav__button-close {
  position: absolute;
  z-index: 10;
  top: 40px;
  right: 40px;
  display: block;
  width: 30px;
  height: 30px;
  background: url("../img/icons/cross.svg") no-repeat;
  border: none;
}
@media (min-width: 1210px) {
  .main-nav__button-close {
    display: none;
  }
}

.main-nav__list {
  position: relative;
  z-index: 10;
  margin: rem(60px 0 0 25px);
  padding: 0;
  list-style-type: none;
}
@media (min-width: 1210px) {
  .main-nav__list {
    position: static;
    margin: 0;
    display: flex;
    gap: rem(67px);
  }
}

.main-nav__link {
  font-size: rem(24px);
  line-height: rem(64px);
  font-weight: 500;
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 1210px) {
  .main-nav__link {
    font-size: rem(15px);
    line-height: rem(30px);
    font-weight: 500;
  }
}

.contacts {
  order: 3;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 1210px) {
  .contacts {
    width: 32%;
  }
}

.contacts__number {
  color: #ffffff;
}
.contacts__number a {
  margin-bottom: rem(2.5px);
  font-size: 20px;
  line-height: rem(24px);
  font-weight: 500;
  text-decoration: none;
  color: inherit;
}
@media (min-width: 1210px) {
  .contacts__number a {
    font-size: rem(24px);
    line-height: rem(29px);
  }
}

.contacts__schedule {
  margin: 0;
  font-size: rem(12px);
  line-height: rem(14px);
  font-weight: 400;
}
@media (min-width: 1210px) {
  .contacts__schedule {
    font-size: rem(16px);
    line-height: rem(19px);
  }
}

.contacts__socials {
  width: 42%;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 40px;
  gap: 6px;
  list-style-type: none;
}
@media (min-width: 1210px) {
  .contacts__socials {
    width: 34%;
  }
}

.contacts__socials-item {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}
.contacts__socials-item a {
  display: block;
  height: 100%;
}
.contacts__socials-item--telegram {
  background: #02adff;
}
.contacts__socials-item--telegram a {
  background: url("../img/icons/icon-telegram.svg") no-repeat center center;
}
.contacts__socials-item--telegram:hover {
  background: #0096de;
}
.contacts__socials-item--whatsapp {
  background: #48db45;
}
.contacts__socials-item--whatsapp a {
  background: url("../img/icons/icon-whatsapp.svg") no-repeat center center;
}
.contacts__socials-item--whatsapp:hover {
  background: #21c31d;
}